import React, {Component} from 'react';
import sampleAstros from '../constellation.json';
const sample = [
  {
    "target": {"name": "Hangyul", "alt":"" },
    "ra": { "decimal": 83.6330833, "h":"05", "m":"34", "s":"31.940" },
    "dec": { "decimal": 22.0145000, "d": "+22", "m":"00", "s":"52.200" }
  },{
    "target": {"name": "Guijin", "alt":"" },
    "ra": { "decimal": 323.3625833, "h":"21", "m":"33", "s":"27.020" },
    "dec": { "decimal": -0.8232500, "d": "-00", "m":"49", "s":"23.700" }
  },{
    "target": {"name": "Dr.Song", "alt":"" },
    "ra": { "decimal": 205.5484167, "h":"13", "m":"42", "s":"11.620" },
    "dec": { "decimal": 28.3772778, "d": "+28", "m":"22", "s":"38.200" }
  },{
    "target": {"name": "Metadium", "alt":"" },
    "ra": { "decimal": 245.8967500, "h":"16", "m":"23", "s":"35.220" },
    "dec": { "decimal": -26.5257500, "d": "-26", "m":"31", "s":"32.700" }
  }
];

this.planetarium;

export default class VirtualSky extends Component {

  componentDidMount() {
    this.$sky = $(this.sky);
    this.planetarium = $.virtualsky({
      id: 'starmap',
      projection: 'stereo',
      'constellations': true,
      objects: sample,
      objectCallback: (o) => console.log('object callback called' + o)
    })
  }

  moveTo() {
    this.planetarium.panTo(56.8690917,24.1053111,3000);
  }

  render() {
    return (
        <div>
          <div ref={sky => this.sky = sky} id={"starmap"} style={styles.container}></div>
          <h1>{this.props.mapShow ?
            'True' : 'False'
          }</h1>
          <button onClick={() => this.moveTo()}>이동</button>
        </div>
    );
  }

}

const styles = {
  container: {
    height: 400,
    width: '100%'
  }
};



